/* Header.scss */
.header{
  position: fixed; left: 0; right: 0; top: 0;
  display: flex; flex-direction: column;
  width: 100%; height: 64px;
  color: #fff;
  padding: 0 15px;
  background: #513736;
  box-sizing: border-box;
}

.status_bar{
  display: flex; flex-direction: row; justify-content: space-between; align-items: center;
  height: 37.5%;
  >div{
    flex: 1;
    /* border: 1px solid red; */
  }
  :nth-child(1){
    text-align: left;
  }
  :nth-child(2){
    text-align: center;
    >span{
      letter-spacing: 1px;
    }
  }
  :nth-child(3){
    text-align: right;
  }
  >div>*{
    padding: 3px;
  }
}

.title_bar{
  display: flex; flex-direction: row; justify-content: space-between; align-items: center;
  height: 62.5%;
  >*{
    flex: 1;
    font-size: 16px;
    /* border: 1px solid red; */
  }
  >h1{
    order: 2;
    text-align: center;
    >span{
      color: #f8ea65;
    }
  }
  >.left_item{
    order: 1;
    text-align: left;
    >a{
      color: #fff;
      opacity: 0.5;
    }
  }
  >.right_item{
    order: 3;
    font-size: 20px;
    text-align: right;
    >a{
      color: #fff;
    }
  }
}